.instructions {
    border: 1px solid darken($off-white,10%);
    border-radius: 5px;
    margin-top: $lg;
    margin-bottom: $lg;
    padding:$sm $sm;
    h1 {
        color: $off-black;
        font-size: $md;
        text-align: center;
    
    }
    .lifeline {
        cursor: pointer;
    }
    .browser-default {
        padding-left:$lg;
        margin-top:10px;
        margin-bottom:15px

    }
    img {
        width: 80%;
        height:50%;
        max-width: 400px;
        max-height: 350px;
        margin:10px;
    }
}

@media(max-width:$md-breakpoint) {
    .instructions img {
        width: 100%;
        height: 150%;
    }
}